"use client";


const BecomeSellerBanner = () => {
    return (
        <section className="bg-white rounded-xl p-6 shadow-sm flex justify-between items-center">
            <div>
                <h3 className="font-semibold">
                    Become a seller, List items now
                </h3>
                <p className="text-sm text-gray-500">
                    Sell new items or used items you no longer need
                </p>
            </div>

            <button className="bg-green-800 text-white px-6 py-2 rounded-full">
                Sell Now
            </button>
        </section>
    );
};

export default BecomeSellerBanner;
