"use client";

import React from "react";
import { formatNaira } from "@/shared/utils/currency.util";

const RecentOrderHistory = () => {
    return (
        <section className="bg-white rounded-3xl p-8 shadow-sm">
            <div className="flex justify-between items-center mb-4">
                <div>
                    <h3 className="font-semibold">Recent Order History</h3>
                    <p className="text-sm text-gray-500">
                        Your top 5 recent orders
                    </p>
                </div>

                <button className="border border-orange-500 text-orange-500 px-4 py-2 rounded-full text-sm">
                    See All Order
                </button>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {[1, 2].map((_, idx) => (
                    <div
                        key={idx}
                        className="border rounded-xl p-4"
                    >
                        <div className="flex justify-between mb-2">
                            <div>
                                <p className="font-medium">
                                    Order ID: #024588
                                </p>
                                <p className="text-xs text-gray-500">
                                    Order date: 12th Sep, 2025 - 03:08pm
                                </p>
                            </div>
                            <span className="font-semibold text-gray-900 text-lg">
                                {formatNaira(320)}
                            </span>
                        </div>

                        <div className="flex gap-3 mt-4">
                            {[1, 2, 3, 4].map((i) => (
                                <div
                                    key={i}
                                    className="w-16 h-16 bg-gray-200 rounded-lg"
                                />
                            ))}
                        </div>
                    </div>
                ))}
            </div>
        </section>
    );
};

export default RecentOrderHistory;
